.App {
  text-align: center;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.link {
  padding: 10px 5px 0 5px;
}

.Item{
  display: flex;
  flex-direction: row;
}

.Tano-logo {
  background-image: url('/src/assets/tano.jpeg');
  background-repeat: no-repeat;
  background-size: cover;
  width: 30px;
  height: 30px;
}

.Discord-logo {
  background-image: url('/src/assets/discord-logo.png');
  background-repeat: no-repeat;
  background-size: cover;
  width: 30px;
  height: 30px;
}

.countdown {
  border-bottom: 2px solid #000;
}

.clima {
  font-size: 10px;
  margin: 0px 10px;
}

.climaTitulo {
  margin: 15px 0px 5px;
  text-align: center;
  font-family: arial, sans-serif;
}

.climaRow {
  text-align: center;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  border: 3px solid #ddd;
  text-transform: capitalize;
  width: 30px;
}

.climaImg {
  width: 50%;
  height: 50%;
}

.imgCell {
  display:block;
}

.climaRow td, .climaRow th {
  border: 1px solid #ddd;
  padding: 4px;
}

.climaHeader th {
  text-align: center;
  background-color: #4CAF50;
  color: white;
}

@media only screen and (min-width: 768px) {
  .Tano-logo {
    width: 50px;
    height: 50px;
  }
  
  .Discord-logo {
    width: 50px;
    height: 50px;
  }

  .clima {
    font-size: 14px;
  }
}